.left-side__list {
  position: absolute;
  list-style-type: none;
  padding-left: 20px;
  top: 52%;

  @media (max-width: 1199px) {
    padding: 0;
    top: 44%;
  }

  @media (max-width: 767px) {
    top: 41%;
  }
}

.left-side__item {
  display: block;
  font-family: 'Roboto Condensed';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 150%;
  align-items: center;
  text-align: right;
  color: #FFFFFF;
  text-align: end;

  @media (max-width: 1199px) {}
}

.left-side__block {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;

  @media (max-width: 1199px) {}
}

.left-side__city {
  display: block;
  margin: 0;
  margin-left: auto;
  margin-top: 111px;
  width: 133px;
  font-family: 'Alumni Sans';
  font-style: normal;
  font-weight: 600;
  font-size: 48px;
  line-height: 58px;
  align-items: center;
  letter-spacing: 0.03em;
  color: #FFFFFF;

  @media (max-width: 1199px) {
    margin: 49px 3px 0 auto;
    font-size: 34px;
    line-height: 41px;
    text-align: end;
  }

  @media (max-width: 767px) {
    margin: 26px 3px 0 auto;
    font-weight: 600;
    font-size: 28px;
    line-height: 34px;
  }
}

.left-side__title {
  display: block;
  max-width: 298px;
  margin: 15px 0 0 auto;
  font-family: 'Alumni Sans';
  font-style: normal;
  font-weight: 600;
  font-size: 92px;
  line-height: 110px;
  text-align: right;
  letter-spacing: 0.05em;
  color: #FFFFFF;
  text-shadow: #FFFFFF 2px 0px, #FFFFFF 2px 2px, #FFFFFF 0px 2px, #FFFFFF -2px 2px, #FFFFFF -2px 0px, #FFFFFF -2px -2px, #FFFFFF 0px -2px, #FFFFFF 2px -2px;
  text-transform: uppercase;

  @media (max-width: 1199px) {
    margin: 8px 0 0 auto;
    max-width: 235px;
    font-weight: 600;
    font-size: 72px;
    line-height: 86px;
    letter-spacing: 0.05em;
    text-transform: none;
  }

  @media (max-width: 767px) {
    margin: 14px 0 0 auto;
    max-width: 213px;
  }
}

.left-side__button {
  display: block;
  width: 100%;
  margin-top: 48px;
  margin-left: auto;
  padding: 19px 11px;
  font-family: "Roboto Condensed";
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: .05em;
  text-transform: uppercase;
  color: #fff;
  border: 4px solid #fff;
  text-align: center;
  max-width: 230px;

  @media (max-width: 1199px) {
    max-width: 185px;
    margin-top: 36px;
    padding: 19px 27px;
  }

  @media (max-width: 767px) {
    max-width: 290px;
    margin-top: 24px;
  }
}