.subscription {
  padding: 0 var(--side-offset, 20px);
  padding-top: 120px;
  background: #F8F9FC;
  padding-bottom: 120px;

  @media (max-width: 1199px) {
    padding-top: 102px;
  }

  @media (max-width: 1199px) {
    padding-top: 64px;
  }
}

.subscription__row {
  position: relative;
}

.subscription__title {
  margin: 0;
  display: block;
  text-align: center;
  font-family: 'Alumni Sans';
  font-style: normal;
  font-weight: 600;
  font-size: 54px;
  line-height: 65px;
  letter-spacing: 0.03em;
  color: #102654;

  @media (max-width: 1199px) {
    font-size: 42px;
    line-height: 50px;
  }

  @media (max-width: 767px) {
    font-size: 32px;
    line-height: 38px;
    align-items: center;
  }
}

.subscription__image {
  position: absolute;
  right: -300px;
  top: 350px;
}